<template>
  <div>
    <!-- 头部设置 -->
    <header>
      <span><van-icon name="arrow-left" @click="lwh_go()" /></span>
      <p>信息填写</p>
      <span @click="lwh_save">保存</span>
    </header>
    <!-- 昵称设置 -->
    <div class="lwh_main">
      <div v-if="this.$route.query.tit == '昵称'">
        <div class="lwh_cont">
          {{ lwh_val }}
        </div>
      </div>
      <!-- 性别选择 -->
      <div v-if="this.$route.query.tit == '性别'">
        <div
          class="lwh_cont"
          :class="{ lwh_sixDef: index == lwh_sixIndex }"
          v-for="(item, index) in lwh_sixValue"
          :key="index"
          @click="lwh_sixBtn(item, index)"
        >
          {{ item }}
        </div>
      </div>
      <!-- 学科选择 -->
      <div v-if="this.$route.query.tit == '学科'">
        <div class="lwh_cont" v-for="(item, index) in lwh_grade" :key="index">
          <van-checkbox
            shape="square"
            v-model="item.flag"
            @click="lwh_gradeBtn"
            >{{ item.cont }}</van-checkbox
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  // 组件注册
  components: {},
  // 父组件传参
  props: {},
  // 变量声明
  data() {
    return {
      lwh_val: null,
      //   全局变量
      lwh_sixcont: null,
      // 性别选择
      lwh_sixValue: ["男", "女"],
      lwh_sixIndex: null,
      // 性别选择
      //  学科信息
      lwh_grade: [
        { cont: "语文", flag: false },
        { cont: "数学", flag: false },
        { cont: "英语", flag: false },
        { cont: "物理", flag: false },
        { cont: "化学", flag: false },
        { cont: "生物", flag: false },
        { cont: "信息技术", flag: false },
      ],
      // 学科信息
    };
  },
  // Vue实例创建完成以后执行 数据可用
  created() {},
  // Vue模板挂载完毕以后执行  DOM节点加载完毕了（可以获取节点了）
  mounted() {
    this.lwh_btn();
  },
  // vue组件更新完毕以后执行
  update() {},
  // 定义事件
  methods: {
    lwh_btn() {
      console.log(this.$route.query);
      this.lwh_val = this.$route.query.item;
    },
    lwh_go() {
      this.$router.go(-1);
    },
    lwh_sixBtn(item, index) {
      this.lwh_sixIndex = index;
      this.lwh_sixcont = item;
    },
    lwh_gradeBtn() {
      let a = this.lwh_grade.filter((i) => {
        return i.flag;
      });
      console.log(a);
      this.lwh_sixcont = a.map((i)=>{
          if(i.flag){
            return  i.cont
          }
      }).join(",")
      console.log(this.lwh_sixcont);
    },
    // 保存
    lwh_save() {
      this.$store.commit("lwh_saveBtn", {
        type: this.$route.query.tit,
        value: this.lwh_sixcont,
      });
      this.$router.go(-1);
    },
  },
  // 过滤器
  filter: {},
  // 计算属性
  computed: {},
  // 监听属性
  watch: {},
};
</script>

<style lang="scss" scoped>
header {
  width: 100%;
  height: 0.48rem;
  display: flex;
  align-items: center;
  > span {
    width: 0.46rem;
    text-align: center;
  }
  > p {
    flex: 1;
    text-align: center;
    font-size: 0.18rem;
  }
}
.lwh_main {
  width: 100%;
  height: 100%;
  background: #f0f2f5;
  padding: 0.1rem 0;
  box-sizing: border-box;
}
.lwh_cont {
  width: 100%;
  box-sizing: border-box;
  padding: 0 0.12rem;
  background: #fff;
  height: 0.58rem;
  line-height: 0.58rem;
  display: flex;
  align-items: center;
}
.lwh_sixDef {
  color: #eb6100;
}
</style>